export class SelectHelper {

    constructor () {

    }


    valueCutter (prefix, source) {
        return source.substring(prefix.length, source.length);
    }

    getLabel (array, value) {
        let ret: string = '';

        if (array && array.length) {
            let t = array.filter(i => i.value === value);
            if (t && t.length) {
                ret = t[0].label;
            }
        }

        return ret;
    }

    getLabels (array, values) {
        let ret = [];

        if (array && array.length) {
            let arr = values.split(',');
            if (arr && arr.length) {
                arr.forEach(element => {
                    let result = this.getLabel(array, element);
                    if (result) {
                        ret.push(result);
                    }
                });
            }
        }

        return ret;
    }
    
    //  1
    //   valuevlauefirstClass: "100", secondClass: "100201"
    itemsfilter (key: string, list: any) {
        let ret = [];

        list.forEach(element => {
            if (element.value.indexOf(key) === 0) {
                ret.push(element);
            }
        });

        return ret;
    }


    //  2
    // 
    // { label: '', value: '101202' }               
    // { label: '', value: '202', parent: '101' }   
    itemsFilterByParent (key: string, list: any) {
        let ret = [];
        
        list.forEach(element => {
            if (element.parent == key) {
                ret.push(element);
            }
        });
        
        return ret;
    }
    itemsFilterByParentList (keys: any, list: any) {
        let ret = [];

        list.forEach(element => {
            if (keys.indexOf(element.parent) != -1) {
                ret.push(element);
            }
        });

        return ret;
    }


    //  3
    //   
    //  value  
    itemsfilterByListKey (keys: any, list: any) {
        let ret = [];

        list.forEach(element => {
            if (this.isValueExist(element.value, keys)) {
                ret.push(element);
            }
        });

        return ret;
    }

    //   
    isValueExist (key: string, list: any) {
        let ret = false;

        for (let i = 0, len = list.length; i < len; i++) {
            let element = list[i];
            if (key.indexOf(element) === 0) {
                ret = true;
                break;
            }
        }

        return ret;
    }

}
